.App {
  --header_height: 60px;
  max-width: 1280px;
  margin: 0 auto;
  padding: 20px;
  padding-top: 70px;
}

.hw {
  margin-bottom: 30px;
  border: 1px solid rgb(97, 96, 96);
  box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03),
    0px 1px 2px rgba(15, 15, 15, 0.582);
  border-radius: 5px;
  padding: 20px;
}

.hwTitle {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  color: #000000;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}
